'use client';

import { useState, useEffect, FormEvent } from 'react';
import axios from '@/lib/axios';
import { toast } from 'sonner';
import { Plus, Pencil, Trash2, Loader2, Scale } from 'lucide-react';

// Komponen UI
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from '@/components/ui/table';
import {
    Dialog,
    DialogContent,
    DialogDescription,
    DialogFooter,
    DialogHeader,
    DialogTitle,
    DialogTrigger,
} from '@/components/ui/dialog';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';

// Tipe Data Satuan (Updated: Tanpa Singkatan)
interface Satuan {
    id: number;
    nama_satuan: string; // cth: Kilogram, Pcs
}

export default function SatuanPage() {
    // State
    const [satuans, setSatuans] = useState<Satuan[]>([]);
    const [loading, setLoading] = useState(true);
    
    const [isDialogOpen, setIsDialogOpen] = useState(false);
    const [isSaving, setIsSaving] = useState(false);
    const [editId, setEditId] = useState<number | null>(null);
    
    // Form State (Updated: Hanya nama_satuan)
    const [formData, setFormData] = useState({
        nama_satuan: '',
    });

    // 1. Fetch Data
    const fetchSatuans = async () => {
        setLoading(true);
        try {
            const response = await axios.get('/api/satuans');
            setSatuans(response.data);
        } catch (error) {
            console.error("Error:", error);
            toast.error("Gagal memuat data satuan.");
        } finally {
            setLoading(false);
        }
    };

    useEffect(() => {
        fetchSatuans();
    }, []);

    // 2. Reset Form
    const resetForm = () => {
        setFormData({ nama_satuan: '' });
        setEditId(null);
        setIsDialogOpen(false);
    };

    // 3. Buka Modal
    const handleOpenDialog = (satuan?: Satuan) => {
        if (satuan) {
            setEditId(satuan.id);
            setFormData({
                nama_satuan: satuan.nama_satuan,
            });
        } else {
            resetForm();
        }
        setIsDialogOpen(true);
    };

    // 4. Submit (Create & Update)
    const handleSubmit = async (e: FormEvent) => {
        e.preventDefault();
        setIsSaving(true);

        try {
            if (editId) {
                // Update (PUT)
                await axios.put(`/api/satuans/${editId}`, formData);
                toast.success("Satuan berhasil diperbarui!");
            } else {
                // Create (POST)
                await axios.post('/api/satuans', formData);
                toast.success("Satuan berhasil ditambahkan!");
            }

            resetForm();
            fetchSatuans();
            
        } catch (error: any) {
            const pesan = error.response?.data?.message || "Gagal menyimpan data.";
            toast.error(pesan);
        } finally {
            setIsSaving(false);
        }
    };

    // 5. Delete
    const handleDelete = async (id: number) => {
        if(!confirm("Yakin ingin menghapus satuan ini?")) return;
        try {
            await axios.delete(`/api/satuans/${id}`);
            toast.success("Satuan berhasil dihapus");
            fetchSatuans();
        } catch (error) {
            toast.error("Gagal menghapus satuan (Mungkin sedang digunakan oleh produk).");
        }
    }

    return (
        <div className="space-y-6">
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
                <div>
                    <h1 className="text-2xl font-bold text-gray-900">Data Satuan</h1>
                    <p className="text-gray-500 text-sm">Atur satuan pengukuran (Kg, Pcs, Porsi, dll).</p>
                </div>
                
                <Button 
                    className="bg-[#D93F21] hover:bg-[#b9351b]"
                    onClick={() => handleOpenDialog()}
                >
                    <Plus className="w-4 h-4 mr-2" />
                    Tambah Satuan
                </Button>
            </div>

            {/* Modal Dialog */}
            <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
                <DialogContent className="sm:max-w-[400px]">
                    <DialogHeader>
                        <DialogTitle>{editId ? 'Edit Satuan' : 'Tambah Satuan Baru'}</DialogTitle>
                        <DialogDescription>
                            Satuan digunakan untuk menghitung stok dan porsi.
                        </DialogDescription>
                    </DialogHeader>
                    
                    <form onSubmit={handleSubmit} className="space-y-4 mt-2">
                        <div className="space-y-2">
                            <Label htmlFor="nama">Nama Satuan <span className="text-red-500">*</span></Label>
                            <Input 
                                id="nama" 
                                placeholder="Contoh: Kilogram / Porsi / Pcs" 
                                value={formData.nama_satuan}
                                onChange={(e) => setFormData({...formData, nama_satuan: e.target.value})}
                                required
                            />
                        </div>
                        
                        {/* Input Singkatan DIHAPUS di sini */}

                        <DialogFooter className="pt-4">
                            <Button type="button" variant="outline" onClick={resetForm}>Batal</Button>
                            <Button type="submit" disabled={isSaving} className="bg-[#D93F21] hover:bg-[#b9351b]">
                                {isSaving && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                                Simpan
                            </Button>
                        </DialogFooter>
                    </form>
                </DialogContent>
            </Dialog>

            {/* Tabel */}
            <Card>
                <CardHeader className="pb-3">
                    <CardTitle className="text-lg">Daftar Satuan Unit</CardTitle>
                </CardHeader>
                <CardContent>
                    <div className="rounded-md border">
                        <Table>
                            <TableHeader>
                                <TableRow className="bg-gray-50">
                                    <TableHead className="w-[50px] text-center">No</TableHead>
                                    <TableHead>Nama Satuan</TableHead>
                                    <TableHead className="text-right">Aksi</TableHead>
                                </TableRow>
                            </TableHeader>
                            <TableBody>
                                {loading ? (
                                    <TableRow>
                                        <TableCell colSpan={3} className="h-24 text-center text-gray-500">
                                            <div className="flex justify-center items-center gap-2">
                                                <Loader2 className="h-5 w-5 animate-spin" /> Memuat data...
                                            </div>
                                        </TableCell>
                                    </TableRow>
                                ) : satuans.length === 0 ? (
                                    <TableRow>
                                        <TableCell colSpan={3} className="h-24 text-center text-gray-500">
                                            Belum ada data satuan.
                                        </TableCell>
                                    </TableRow>
                                ) : (
                                    satuans.map((item, index) => (
                                        <TableRow key={item.id} className="hover:bg-gray-50">
                                            <TableCell className="text-center text-gray-500">{index + 1}</TableCell>
                                            <TableCell className="font-medium text-gray-900">
                                                <div className="flex items-center gap-2">
                                                    <Scale className="w-4 h-4 text-gray-400" />
                                                    {item.nama_satuan}
                                                </div>
                                            </TableCell>
                                            <TableCell className="text-right">
                                                <div className="flex justify-end gap-2">
                                                    <Button 
                                                        variant="ghost" size="icon" 
                                                        className="h-8 w-8 text-blue-600 hover:bg-blue-50"
                                                        onClick={() => handleOpenDialog(item)}
                                                    >
                                                        <Pencil className="h-4 w-4" />
                                                    </Button>
                                                    <Button 
                                                        variant="ghost" size="icon" 
                                                        className="h-8 w-8 text-red-600 hover:bg-red-50"
                                                        onClick={() => handleDelete(item.id)}
                                                    >
                                                        <Trash2 className="h-4 w-4" />
                                                    </Button>
                                                </div>
                                            </TableCell>
                                        </TableRow>
                                    ))
                                )}
                            </TableBody>
                        </Table>
                    </div>
                </CardContent>
            </Card>
        </div>
    );
}